  import { mapGetters,mapActions }from 'vuex'
export default {
  data(){
    return{
      dataList:{ list:[]}
    }
  },
  created(){
    // localStorage.setItem('token','Bearer eyJhbGciOiJIUzUxMiJ9.eyJzdWIiOiIxNzc1NTc1NTY3NiIsInNjb3BlcyI6WyJVU0VSIl0sImlzcyI6Imh0dHA6Ly95dWFvcS5jb20iLCJpYXQiOjE1MjY4ODcwMzksImV4cCI6MTU0NDg4NzAzOX0.JP90fTfrOmDqlVHHyTKJg8WP9TB-O-wdTXX6Xd41xkFJhYLguQf3jUVcY4cvc_d5O0nCBf3laQLtS0AOWjcl8w')
    if(this.$route.query.token){
      localStorage.setItem('token',decodeURIComponent(this.$route.query.token))
    }
    if(this.$route.query.refreshToken){
      localStorage.setItem('refreshToken',decodeURIComponent(this.$route.query.refreshToken))
    }
    // localStorage.setItem('refreshToken','Bearer eyJhbGciOiJIUzUxMiJ9.eyJzdWIiOiIxMTEiLCJzY29wZXMiOlsiUk9MRV9SRUZSRVNIX1RPS0VOIl0sImlzcyI6Imh0dHA6Ly95dWFvcS5jb20iLCJqdGkiOiJmODZhNjI0OS01YjhjLTQ3NTUtODdiMi1lYTg0NzU3MjQ0YjQiLCJpYXQiOjE1MjUzMTE2NjUsImV4cCI6MTUyNTkxNjQ2NX0.EjUU_NVxzNOIr0mEG9Z5e-urBrCqQIZvmLSrw1SSiWkB22-GingxkUvah-UTRefw0s1tV8yJkieCMdYGJXXjmw')
    this.showLoading()
    this.refundLog({this:this,data:{returnId:this.$route.query.returnId},method:this.refundLog})
    this.physicalRefundDetail({this:this,data:{orderId:this.$route.query.id},method:this.physicalRefundDetail})
  },
  mounted(){
    document.title = '审核进度'
  },
  methods:{
    ...mapActions(['refundLog','physicalRefundDetail']),
    getData(data,i){
      let obj={}
      if(i){
        obj.list = data
        this.dataList ={...this.dataList,...obj}
      }else{
        obj.returnId = data.returnId
        obj.refundTime = data.refundTime
        obj.status = data.status
        obj.refundType = data.refundType
        this.dataList ={...this.dataList,...obj}
      }
    }
  },
  filters:{
    formTime(c){
      if(!c){
        return ''
      }
      let time =  new Date(c)
      let Y= time.getFullYear()
      let M= time.getMonth() +1 <10? '0'+ time.getMonth():time.getMonth() +1
      let D= time.getDate()
      let h= time.getHours()
      let m= time.getMinutes()
      let s= time.getSeconds()
      return Y+'-'+M+'-'+D+' '+h+':'+m+':'+s
    }
  }
}



